*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,body{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.App{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App h2{
  font-size: 100px;
}

.App button{
  outline: none;
  border: 0;
  border-radius: 10px;
  font-size: 25px;
  background-color: rgb(51, 140, 255);
  border-bottom: 3px solid #ccc;
  color: #fff;
  width: 300px;
  cursor: pointer;
  height: 50px;
}